import "./FreeTime.css";
import React, { useRef, useState } from "react";
// Import Swiper React components

import { useEffect } from "react";
import ArrlvalMenu1 from "./ArrlvalMenu1";
import ArrlvalMenu2 from "./ArrlvalMenu2";

export default function Arrival() {
  let [p, setp] = useState(0);
  return (
    <>
      <div style={{ marginTop: "80px" }}>
        <div className="FreeTimebox">
          <div className="Free_title">
            <h2>NEW ARRIVAL</h2>
            <ul className="dmdk">
              <li
                style={{
                  color: "#fff",
                  border: "1px solid #d28f8a",
                  background: "#d28f8a",
                }}
                onClick={()=>{ setp(0) }}
                className="ws"
              >
                KIDS
              </li>
              <li onClick={()=>{ setp(1) }} className="swe">MLB</li>
            </ul>
          </div>

          <TabContent p={p}/>
        </div>
      </div>
    </>
  );
}

function TabContent({p}){
    let [fade, setFade] = useState('')
     useEffect( ()=>{
      setTimeout(()=>{ setFade('end')},20)
      return ()=>{
          setFade('')
      }
     } ,[p])
  
    return (
      <div className={'start ' + fade}>
        { [<ArrlvalMenu1 />, <ArrlvalMenu2 />][p] }
      </div>
    )
  }
