import { Swiper, SwiperSlide } from "swiper/react";
import { Scrollbar } from "swiper";
import FreeData from "./FreeTimeData";
import { useState } from "react";
import { useNavigate, } from "react-router-dom";

function FreeTimeMenu1 (){
    let navigate = useNavigate()
  let [res1, setRes1] = useState([20,21,22]);
    return(
        <div className="bg_img">
        <div className="twxt">
            <span style={{fontWeight:'600',fontSize:'18px'}}>HIP  </span>
            <h1 style={{marginTop:'10px',fontSize:'40px',fontWeight:'600'}}>#MLB</h1>
        </div>
        <div className="rightbox">
          <Swiper
            slidesPerView={2.5}
            spaceBetween={30}
            scrollbar={{
              hide: false,
            }}
            modules={[Scrollbar]}
            className="mySwiper"
          >
            {FreeData.map((a,i) => {
              return (
                <SwiperSlide className="scroll" style={{display:"block"}}  onClick={() => {navigate('/detail/'+res1[i])}}>
                  <img src={a.person} alt="" />  
                  <img src={a.url} alt="" />
                  <div className="collecton_textbox">
                    <p>{a.name}</p>
                    <span>{a.price}</span>
                  </div>
                </SwiperSlide>
              );
            })}
          </Swiper>
        </div>
      </div>
    )
}

export default FreeTimeMenu1
