import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";

import "./slider.css";

// import required modules
import { Pagination,Navigation,Autoplay} from "swiper";
import slider_img from "./slider_img.js";

function Slider() {
  return (
    <>
      <Swiper
        slidesPerView={1}
        spaceBetween={0}
        loop={true}
        autoplay={{
            delay:5000,
        }}
        pagination={{
          clickable:true,
        }}
        navigation={true}
        modules={[Pagination, Navigation,Autoplay]}
        className="mySwiper"
      >
        {slider_img.map((a,i)=>{
          return (
            <SwiperSlide style={{position:'relative'}}>

              <p className="text_box">
                <span className={slider_img[i].id}>{slider_img[i].title}</span>
                <br/>
                <b className={slider_img[i].id}>{slider_img[i].title2}</b>
                <br/>
                <p className={slider_img[i].id}>{slider_img[i].desc}</p>
              </p>


            <img src={slider_img[i].url} alt=""/>
            </SwiperSlide>
          ) 
        })}
      </Swiper>
    </>
  );
}

export default Slider
