import "./Video.css";
import React, { useRef, useState } from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";

// import required modules
import { Navigation } from "swiper";

function Video() {
  return (
    <div className="Video_box">
      <div className="Video_title">
        <h2>Video</h2>
        <div className="video_box">
          <Swiper
            loop={true}
            slidesPerView={1}
            spaceBetween={30}
            navigation={true}
            modules={[Navigation]}
            className="mySwiper"
          >
            <SwiperSlide style={{ width: "750px" }}>
              <video controls>
                <source src="./video/MLB_aespa1.mp4" type="video/mp4" />
              </video>
            </SwiperSlide>
            <SwiperSlide style={{ width: "750px" }}>
              <video controls>
                <source src="./video/MLB_aespa2.mp4" type="video/mp4" />
              </video>
            </SwiperSlide>
            <SwiperSlide style={{ width: "750px" }}>
              <video controls>
                <source src="./video/MLB_ss.mp4" type="video/mp4" />
              </video>
            </SwiperSlide>
            <SwiperSlide style={{ width: "750px" }}>
              <video controls>
                <source src="./video/MLB_video.mp4" type="video/mp4" />
              </video>
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </div>
  );
}

export default Video;
