import "./FreeTime.css";
import React, { useRef, useState } from "react";
// Import Swiper React components

import FreeTimeMenu1 from "./FreeTimeMenu1";
import FreeTimeMenu2 from "./FreeTimeMenu2";
import FreeTimeMenu3 from "./FreeTimeMenu3";
import FreeTimeMenu4 from "./FreeTimeMenu4";

// Import Swiper styles
import "swiper/css";
import "swiper/css/scrollbar";

// import required modules

import { useEffect } from "react";

export default function FreeTime() {
  let [ta, setTa] = useState(0);
  return (
    <>
      <div style={{ marginTop: "80px" }}>
        <div className="FreeTimebox">
          <div className="Free_title">
            <h2>COLLECTON</h2>
            <ul className="ok">
              <li
                style={{
                  color: "#fff",
                  border: "1px solid #000",
                  background: "#000",
                }}
                onClick={()=>{setTa(0)}}
              >
                
              </li>
              <li onClick={()=>{setTa(1)}}></li>
              <li onClick={()=>{setTa(2)}}> </li>
              <li onClick={()=>{setTa(3)}}> </li>
            </ul>
            
          </div>

          <TabContent ta={ta}/>
        </div>
      </div>
    </>
  );
}


function TabContent({ta}){
  let [fade, setFade] = useState('')
   useEffect( ()=>{
    setTimeout(()=>{ setFade('end')},20)
    return ()=>{
        setFade('')
    }
   } ,[ta])

  return (
    <div className={'start ' + fade}>
      { [<FreeTimeMenu1 />,<FreeTimeMenu2 />,<FreeTimeMenu3 />,<FreeTimeMenu4 />][ta] }
    </div>
  )
}
