import ArrlvalData from "./ArrlvalData";
import "./FreeTime.css";
import { useState } from "react";
import { useNavigate, } from "react-router-dom";

function ArrlvalMenu1(){
  let navigate = useNavigate()
  let [res1, setRes1] = useState([23,24,25,26]);
    return(
        <div className="bg_img" id="Arrlval_back">
        <img src="./img/Arrlval_img.jpg" alt="" />

        <div className="rightbox" id="Arrlval_right">
          {ArrlvalData.map((a,i) => {
            return (
              <div className="right_imgbox" onClick={() => {navigate('/detail/'+res1[i])}}>
                <img src={a.person} alt="" />
                <img src={a.url} alt="" />
                <p>{a.name}</p>
                <span>{a.price}</span>
              </div>
            );
          })}
        </div>
      </div>
    )
}

export default ArrlvalMenu1
