import { Swiper, SwiperSlide } from "swiper/react";
import { Scrollbar } from "swiper";
import FreeData from "./FreeTimeData";

function FreeTimeMenu3 (){
    return(
        <div className="bg_img3">
        <div className="twxt">
            <span style={{fontWeight:'600',fontSize:'18px'}}> </span>
            <h1 style={{marginTop:'10px',fontSize:'40px',fontWeight:'600'}}>#MLB</h1>
        </div>
        <div className="rightbox">
          <Swiper
            slidesPerView={2.5}
            spaceBetween={30}
            scrollbar={{
              hide: false,
            }}
            modules={[Scrollbar]}
            className="mySwiper"
          >
            {FreeData.map((a) => {
              return (
                <SwiperSlide className="scroll" style={{display:"block"}}>
                  <img src={a.person3} alt="" />  
                  <img src={a.url3} alt="" />
                  <div className="collecton_textbox">
                    <p>{a.name3}</p>
                    <span>{a.price3}</span>
                  </div>
                </SwiperSlide>
              );
            })}
          </Swiper>
        </div>
      </div>
    )
}

export default FreeTimeMenu3
