import React, { Component } from "react";
import Slider from "react-slick";
import "./Responsive.css";
import Data from "./ResponsiveData.js";
import { useState } from "react";
import { useNavigate, } from "react-router-dom";

export default function Responsive (){
  let navigate = useNavigate()
  let [res1, setRes1] = useState([0,1,2,3,4,5,6,7]);
    var settings = {
      infinite: true,
      speed: 500,
      slidesToShow: 4,
      slidesToScroll: 4,
      initialSlide: 0,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true,
          },
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            initialSlide: 2,
          },
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
          },
        },
      ],
    };
    return (
      <div className="New_Arrivals">
        <div className="first_slider">
          <h2> New Arrivals, New Life </h2>
          <Slider {...settings}>
            {Data.map((a,i) => {
              return (
                <div className="Arrivals_box" onClick={() => {navigate('/detail/'+res1[i])}}>
                  <img src={a.person} alt="" />
                  <img src={a.url} alt="" />
                  <p>{a.name}</p>
                  <span>{a.price}</span>
                </div>
              );
            })}
          </Slider>
        </div>
      </div>
    );

}
