import "./Cart.css";
import { Table } from "react-bootstrap";
import { Button } from "react-bootstrap";
import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { addCount, decreaseCount, deleteItem, sortName } from "./store.js";

function Cartssw() {
  let state = useSelector((state) => state);
  let dispatch = useDispatch();

  const smallProdcuctStyle = {
    border: "1px solid #ddd",
    width: "100px",
    height: "80px",
    cursor: "pointer",
  };

  let textverticalAlign = {
    verticalAlign: "middle",
    textAlign: "center",
  };

  return (
    <div className="C">
      <div className="cart">
        <h3></h3>
        <p>Home {">"} </p>
      </div>

      <div className="section">
        <ul className="dnl">
          <li></li>
          <li>/</li>
          <li></li>
        </ul>

        <ul className="asd">
          <li></li>
          <li></li>
          <li></li>
        </ul>

        <Table style={{ marginTop: "30px" }}>
          <thead>
            <tr>
              <th style={{ verticalAlign: "middle", textAlign: "center" }}>
                id
              </th>
              <th></th>
              <th style={{ verticalAlign: "middle", textAlign: "center" }}>
                
              </th>
              <th style={{ verticalAlign: "middle", textAlign: "center" }}>
                
              </th>
              <th style={{ verticalAlign: "middle", textAlign: "center" }}>
                
              </th>
            </tr>
          </thead>
          <tbody>



          {
              state.cart.map((a, i)=>
              <tr key={i}>
                  <td style={textverticalAlign}>{state.cart[i].id+1}</td>


                  <td>
                   
                  <Link to={`/detail/${state.cart[i].id}`}>
                    <img src={`img/${state.cart[i].imgurl}`} style={smallProdcuctStyle} />
                  </Link>

                  </td>
                  <td style={textverticalAlign}>{state.cart[i].name}</td>
                  <td style={textverticalAlign}>{state.cart[i].count}</td>
                  <td style={textverticalAlign}>
                    <Button onClick={()=>{
                       dispatch(addCount(state.cart[i].id))
                  }} variant="dark" style={{marginRight:'10px'}}>+</Button>

                  <Button onClick={()=>{
                       dispatch(decreaseCount(state.cart[i].id))
                  }} variant="dark" style={{marginRight:'10px'}}>-</Button>

                  <Button onClick={()=>{
                       dispatch(deleteItem(state.cart[i].id))
                  }} variant="dark"></Button>
                
                  </td>              
              </tr>
              )
          }


          </tbody>
        </Table>


      </div>
    </div>
  );
}

export default Cartssw;
