import './MainSection.css';
import React from "react";
import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import MainSlider from './slider/MainSlider.js'
import MainSliderCAP from './slider/MainSliderCAP.js'
import MainSliderSHOES from './slider/MainSliderSHOES.js'
import MainSliderAPPAREL from './slider/MainSliderAPPAREL.js'
import MainSliderBAG from './slider/MainSliderBAG.js'
import MainSliderKIDS from './slider/MainSliderKIDS.js'
import { useState } from 'react';
import { useEffect } from 'react';

function MainSection(){
    let [tap, setTap] = useState(0); 
    return (
      <div className='maina'>
        <div className='mainS'>
              <Navbar
          bg="none"
        >
          <Container style={{ maxWidth: "1298px"}}>
            <Navbar.Brand style={{fontSize:'32px'}}>
              RANKING
            </Navbar.Brand>
            <Nav className="me-auto" style={{marginTop:'5px'}}>
              <Nav.Link className="main" style={{color:'#767676',border:'none'}}></Nav.Link>
            </Nav>
            <Nav style={{marginTop:'5px'}}>
              <Nav.Link className='main_ww' style={{color:'#000',borderBottom:'1px solid #000'}} onClick={()=>{setTap(0)}}>ALL</Nav.Link>
              <Nav.Link className='main_ww' style={{borderBottom:'1px solid #f8f8f8'}} onClick={()=>{setTap(1)}}>CAP</Nav.Link>
              <Nav.Link className='main_ww' style={{borderBottom:'1px solid #f8f8f8'}} onClick={()=>{setTap(2)}}>SHOES</Nav.Link>
              <Nav.Link className='main_ww' style={{borderBottom:'1px solid #f8f8f8'}} onClick={()=>{setTap(3)}}>APPAREL</Nav.Link>
              <Nav.Link className='main_ww' style={{borderBottom:'1px solid #f8f8f8'}} onClick={()=>{setTap(4)}}>BAG</Nav.Link>
              <Nav.Link className='main_ww' style={{borderBottom:'1px solid #f8f8f8'}} onClick={()=>{setTap(5)}}>KIDS</Nav.Link>
            </Nav>
          </Container>
        </Navbar>
        <TabContent tap={tap}/>
        
        </div>
      </div>
    )
  }


  function TabContent({tap}){
    let [fade, setFade] = useState('')
     useEffect( ()=>{
      setTimeout(()=>{ setFade('end')},20)
      return ()=>{
          setFade('')
      }
     } ,[tap])
  
    return (
      <div className={'start ' + fade}>
        { [ <MainSlider />, <MainSliderCAP />, <MainSliderSHOES />,<MainSliderAPPAREL />,<MainSliderBAG />,<MainSliderKIDS /> ][tap] }
      </div>
    )
  }

  export default MainSection;
