import ArrlvalData from "./ArrlvalData";
import "./FreeTime.css";

function ArrlvalMenu2(){
    return(
        <div className="bg_img" id="Arrlval_back">
        <img src="./img/Arrlval_img2.jpg" alt="" />

        <div className="rightbox" id="Arrlval_right">
          {ArrlvalData.map((a) => {
            return (
              <div className="right_imgbox">
                <img src={a.person2} alt="" />
                <img src={a.url2} alt="" />
                <p>{a.name2}</p>
                <span>{a.price2}</span>
              </div>
            );
          })}
        </div>
      </div>
    )
}

export default ArrlvalMenu2
