import Button from "react-bootstrap/Button";
import "./MlbStyling.css";
import MlbStyling_Data from "./MlbStyling_Data";

function MlbStyling() {
  return (
    <div style={{ margin: "0 auto", width: "1275px" }}>
      <h2>MLB STYLING</h2>
      <ul className="Mlb_imgbox">
        {MlbStyling_Data.map((a) => {
          return (
            <li>
              <img src={a.url} alt="" />
              <div className="hover_bar">
                <Button variant="outline-light">  </Button>
              </div>
            </li>
          );
        })}
      </ul>
      <div className="mlb_sty_textbox">
        <h2>   !  </h2>
        <p>
          MLB KIDS  
          <br />
            
        </p>
        <Button variant="dark">  </Button>
      </div>
    </div>
  );
}

export default MlbStyling;
